import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { AssetsServiceService } from 'src/app/core/services/assets-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import * as moment from 'moment';

@Component({
  selector: 'app-asset-edit-formcompoennet',
  templateUrl: './asset-edit-formcompoennet.component.html',
  styleUrls: ['./asset-edit-formcompoennet.component.scss']
})
export class AssetEditFormcompoennetComponent implements OnInit {
  public assetId:any;
  public assetsEditForm:any;
  public submitted = false;
  GLData: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));


  constructor(public dialogRef: MatDialogRef<AssetEditFormcompoennetComponent>,private formBuilder: FormBuilder, public router: Router, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe,private assetsServiceService: AssetsServiceService,  @Inject(MAT_DIALOG_DATA) public assetData: any, ) { }

  ngOnInit(): void {
    this.assetId=this.assetData.assetId
    this.getASSETById();
    this.getASSETData()
    this.createAssetForm();
   
  }
  createAssetForm(){
    this.assetsEditForm = this.formBuilder.group({
      assetDate: ["", [Validators.required]],
      assetName: ["", [Validators.required]],
      toatalAmount: [""],
      
    });
  }
  getASSETData(){
    this.assetsServiceService.getAsseTListByledger({},this.currentUser.id).then((res:any)=>{
      console.log("AssetsByLedegr",res)
      this.GLData = res.data.map((item)=>{ return item.glNumber});
    })

  }
 
  postAssetsDetails(){
    let Body=
    {
        "update": {
            "userId": this.currentUser.id,
          
            "assetsType":"CURRENT",
            "assetName":this.assetsEditForm.controls.assetName.value,
            "date":this.assetsEditForm.controls.assetDate.value,
            "valueOfAsset":this.assetsEditForm.controls.toatalAmount.value,
            "costOfAsset":"",
            "uploadDocument": "",
            "depreciation":"",
            "method":"",
            "process":"",
            "enterDepreciation":"",
            "year": ""
        },
        "additionalDetails":[], 
    }
        
    // let Body =
    // {
    //   "userId": this.currentUser.id,
    //   "assetsType": "CURRENT",
    //   "assetName":  this.assetsEditForm.controls.assetName.value,
    //   "date": this.assetsEditForm.controls.assetDate.value,
    //   "valueOfAsset": this.assetsEditForm.controls.toatalAmount.value,
    // }
    this.assetsServiceService.updateNonCurrentAssets(Body,this.assetId).then((res: any) => {
      // this.toastService.openSnackBar("Current Assets Added Successfully!!!");
      this.toastService.toastMsg({
        title: "Success",
        content: 'Current Assets Updated Successfully!!!',
      });
      this.assetsServiceService.notifyassetsAdded();
      this.dialogRef.close();

    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
      }
    })

  }
  cancel() {
    this.dialogRef.close();
  }
  get f() {
    return this.assetsEditForm.controls;
  }
  openDatepicker() {
     // Check if the current value of taskdate is a Date object
  const selectedDate = this.assetsEditForm.get('assetDate').value;

  if (selectedDate instanceof Date && !isNaN(selectedDate.getTime())) {
  
  } else {
    // Set a default date or any valid date here
    this.assetsEditForm.get('assetDate').setValue(new Date());
  }
  }
  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }
  getASSETById(){
    this.assetsServiceService.getNONCurrentAssetsDataBYId({},this.assetId).then((res:any)=>{
      this.assetsEditForm.patchValue({
        assetDate: res.data.date ? moment(res.data.date).format("DD-MMM-YYYY") : moment(res.data.date).format("DD-MMM-YYYY"),
        assetName: res.data.assetName ? res.data.assetName:null, 
        toatalAmount: res.data.valueOfAsset ? res.data.valueOfAsset:0,
      })
    })
  }
}
