import { Component, OnInit } from '@angular/core';
import { AssetNonFormcompoennetComponent } from '../asset-non-formcompoennet/asset-non-formcompoennet.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { NonAssetEditFormComponent } from '../non-asset-edit-form/non-asset-edit-form.component';
import { AssetsServiceService } from 'src/app/core/services/assets-service.service';
import { DatePipe, TitleCasePipe } from '@angular/common';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { RunDepraciationComponentComponent } from '../run-depraciation-component/run-depraciation-component.component';
import * as ExcelJS from 'exceljs/dist/exceljs.min.js';
import { saveAs } from 'file-saver';

@Component({
  selector: 'app-asset-non-listcompoennet',
  templateUrl: './asset-non-listcompoennet.component.html',
  styleUrls: ['./asset-non-listcompoennet.component.scss']
})
export class AssetNonListcompoennetComponent implements OnInit {
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public NonAseetData:any=[];
  public aseetId:any;
  searchKey: string;
  public currentPageNo: number = 0;
  public partyPageSize: number = 5;
  public p = 1;
  public totalrow: any;
  public toatlPages: any;
  public pagesize: number = 1;
  public isQuarterly: boolean = false;
  public isYearly: boolean = false;
  public isHalfYearly: boolean = false;
  public isMonthly: boolean = false;
  Depreciation: any;
  constructor(private dialog: MatDialog, private assetsServiceService: AssetsServiceService,private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe,private datePipe: DatePipe) { }

  ngOnInit(): void {
    this.assetsServiceService.assetsAdded$.subscribe(() => {
      this.getNonAssetData();
    });
    
    this.getNonAssetData();
  }
  OpenRunDepreciation(){
    const dialogRef: MatDialogRef<RunDepraciationComponentComponent> = this.dialog.open(RunDepraciationComponentComponent, {
      width: '35%',
      height: 'auto',
      data: {},
      panelClass: 'custom-dialog-assets-class',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }
  nonAssetsAddForm(){
    const dialogRef: MatDialogRef<AssetNonFormcompoennetComponent> = this.dialog.open(AssetNonFormcompoennetComponent, {
      width: '55%',
      height: '100vh',
      data: {},
      panelClass: 'custom-dialog-assets-class',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }
 
  openEditModel(NonassetId){
    const dialogRef: MatDialogRef<NonAssetEditFormComponent> = this.dialog.open(NonAssetEditFormComponent, {
      width: '55%',
      height: '100vh',
      data: {NonassetId:NonassetId},
      panelClass: 'custom-dialog-assets-class',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }

  getNonAssetData(){
 
    this.assetsServiceService.getNONCurrentAssetsData({},this.currentUser.id,this.partyPageSize, (this.currentPageNo + 1)).then((res:any)=>{
      this.NonAseetData=res.data.totalDecriptionValue;
      this.p = res.data.currentPage;
      this.totalrow = res.data.totalrows
      this.toatlPages = res.data.totalPages
      if (res.success) {
        this.Depreciation = res.data?.equipmentSetting;
        if (this.Depreciation) {
          this.isMonthly = this.Depreciation.monthly,
          this.isQuarterly = this.Depreciation.quaterly,
          this.isYearly = this.Depreciation.yearly,
          this.isHalfYearly = this.Depreciation.halfyearly
        }
      }
    }, (err) => {
      if (err.error.expose || err.status==404 ) {
        this.NonAseetData=[];
       
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong."
        });
      }
    })

  }
  getAssetsId(aseetId){
    this.aseetId=aseetId
  }
  deleteAsset(){
    this.assetsServiceService.deleteAssets({},this.aseetId).then((res:any)=>{
      this.toastService.toastMsg({
        title: "Success",
        content: "Non Current Assets Deleted Successfully!!!"
      });
      this.getNonAssetData();

    }
    , (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message)
        });
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong."
        });
      }
    })

  }
  roundDepreciationValue(value: number): number {
    return (value - Math.floor(value) >= 0.5) ? Math.ceil(value) : Math.floor(value);
  }

  downlaodAllData(){
    if (this.NonAseetData.length > 0) {
      this.exportToExcel();  
    } else {
      this.toastService.toastMsg({
        title: "Error",
        content: "No Data Available For Download",
      });
    }
  }
  exportToExcel(){
    console.log("EXPORT ___")
    const workbook = new ExcelJS.Workbook();
   // const worksheet = workbook.addWorksheet('Sheet 1');
   const worksheet = workbook.addWorksheet('Sheet 1', {
    views: [
      {
        state: 'frozen',
        xSplit: 2, // Number of columns to freeze (0 means no freezing)
        ySplit: 2, // Number of rows to freeze (e.g., 2 means freeze the first two rows)
      },
    ],
  });
    // Define a header style
    const headerStyle = {
      font: { color: { argb: 'FF0000FF' }, bold: true },
      fill: { type: 'pattern', pattern: 'solid', fgColor: { argb: 'FFFF00FF' } },
    };
    // Define your headers
    const header1 = [
      'Assets Details',
      '',
      '',
      '',
      '',

      'Depreciation',
      '',
      ''
   
    ]
    const headers2 = [
      'Assets Name',
      'Purchase Date',
      'Purchase Value',
      'Scrap Value',
      'Additional Cost',
      'Method',
      'Process',
      'Value',
    ];

    // Apply the header style to the header row
    const headerRow1 = worksheet.addRow(header1);
    const headerRow2 = worksheet.addRow(headers2);
    worksheet.mergeCells('A1:E1');
    worksheet.mergeCells('F1:H1');
    headerRow1.eachCell((cell) => {
      cell.border = {
        top: { style: 'thin' },
        bottom: { style: 'thin' },
        left: { style: 'thin' },
        right: { style: 'thin' },
      };
      cell.fill = headerStyle.fill;
      cell.alignment = { vertical: 'middle', horizontal: 'center', wrapText: true };
      //  cell.width = 120

    });
    headerRow2.eachCell((cell) => {
      cell.border = {
        top: { style: 'thin' },
        bottom: { style: 'thin' },
        left: { style: 'thin' },
        right: { style: 'thin' },
      };
      cell.fill = headerStyle.fill;
      cell.alignment = { vertical: 'middle', horizontal: 'center', wrapText: true };
      //  cell.width = 120

    });

    // Set column widths for header cells
    worksheet.columns.forEach((column, index) => {
      // Assuming you want a fixed width of 120 for all header columns
      column.width = 15;
    });

    // Set background colors for multiple cells using a loop
    const cellBackgroundColors = [
      { cell: 'A1', color: 'FFC6E0B4', width: 5 }, // green background for cell A1
      { cell: 'B1', color: 'FFB4C6E7' },// light blue background for cell A1
      { cell: 'C1', color: 'FFB4C6E7' },
      { cell: 'D1', color: 'FFB4C6E7' },
      { cell: 'E1', color: 'FFB4C6E7' },
      { cell: 'F1', color: 'FFC6E0B4' },//green
      { cell: 'G1', color: 'FFC6E0B4' },
      { cell: 'H1', color: 'FFC6E0B4' },
   



      { cell: 'A2', color: 'FFB4C6E7', width: 5 },//light blue background for cell A1
      { cell: 'B2', color: 'FFB4C6E7' },// light blue background for cell A1
      { cell: 'C2', color: 'FFB4C6E7' },
      { cell: 'D2', color: 'FFB4C6E7' },
      { cell: 'E2', color: 'FFB4C6E7' },
      { cell: 'F2', color: 'FFC6E0B4' },//green
      { cell: 'G2', color: 'FFC6E0B4' },
      { cell: 'H2', color: 'FFC6E0B4' },

   
   

      // FF8DB4E2- blue color code  FFFFC4D5 Orange

    ];
    cellBackgroundColors.forEach((cellColor) => {
      worksheet.getCell(cellColor.cell).fill = {
        type: 'pattern',
        pattern: 'solid',
        fgColor: { argb: cellColor.color },
      };
    });
    this.NonAseetData.forEach((item,index)=>{
      const row =[]
      row.push(item.assetName?item.assetName:'NA');
      const formattedInvoiceDate = this.datePipe.transform(item.date , 'dd MMM yyyy');
      row.push(formattedInvoiceDate ? formattedInvoiceDate : "NA");
      row.push(item.valueOfAsset? parseFloat(item.valueOfAsset).toFixed(2):0.00)
      row.push(item.scrapValue ? parseFloat(item.scrapValue).toFixed(2):0.00)
      row.push(item.costOfAsset ? parseFloat(item.costOfAsset).toFixed(2):0.00)
      row.push(item.method ? item.method:'NA')
      row.push(item.process ? item.process:'NA')
      row.push(item.costOfAsset ? parseFloat(item.costOfAsset).toFixed(2):0.00)
      worksheet.addRow(row);
 
      const vatCellC = worksheet.getCell('C' + worksheet.rowCount);
      // Set the alignment of the VAT cell to right
      vatCellC.alignment = { horizontal: 'right' };
      const vatCellD = worksheet.getCell('D' + worksheet.rowCount);
      // Set the alignment of the VAT cell to right
      vatCellD.alignment = { horizontal: 'right' };
      const vatCellE = worksheet.getCell('E' + worksheet.rowCount);
      // Set the alignment of the VAT cell to right
      vatCellE.alignment = { horizontal: 'right' };
      const vatCellH = worksheet.getCell('H' + worksheet.rowCount);
      // Set the alignment of the VAT cell to right
      vatCellH.alignment = { horizontal: 'right' };
    })
  
   
    workbook.xlsx.writeBuffer().then((buffer) => {
      const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
      saveAs(blob, 'Non Assets.xlsx');
    });

  }
  getAssetListSearch(searchKey){
    if (this.searchKey == "") {
      this.currentPageNo = 0; 
      this.assetsServiceService.getNONCurrentAssetsDataFilter({}, this.currentUser.id, this.partyPageSize, (this.currentPageNo + 1),searchKey.trim()).then((res: any) => {
        this.NonAseetData=res.data.totalDecriptionValue;
        this.p = res.data.currentPage;
        this.totalrow = res.data.totalrows
        this.toatlPages = res.data.totalPages
        if (this.currentPageNo > 0) {
          if (this.NonAseetData.length === 0) {
            this.currentPageNo = 0;
            this.getAssetListSearch(this.searchKey);
          }
        }
      })
    }
    else {
      this.currentPageNo = 0; 
    this.assetsServiceService.getNONCurrentAssetsDataFilter({}, this.currentUser.id,this.partyPageSize, (this.currentPageNo + 1),searchKey.trim()).then((res: any) => {
      this.NonAseetData=res.data.totalDecriptionValue;
      this.p = res.data.currentPage;
      this.totalrow = res.data.totalrows
      this.toatlPages = res.data.totalPages
      if (this.currentPageNo > 0) {
        if (this.NonAseetData.length === 0) {
          this.currentPageNo = 0;
          this.getAssetListSearch(this.searchKey);
        }
      }
      }, (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
        }
      }
      );
    }
  }
  handlePage(e) {
    this.currentPageNo = e.pageIndex;
    this.pagesize = e.pageSize;
    if (this.NonAseetData.length > 0) {
      this.partyPageSize = e.pageSize;
      const searchInput = document.getElementById('searchInput') as HTMLInputElement;
      if (searchInput.value !== '') {
        this.getAssetListSearch(searchInput.value);
      } else {
        this.getNonAssetData();
      }
      }
    }
  }

