import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { AssetsServiceService } from 'src/app/core/services/assets-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { ChangeDetectorRef } from '@angular/core';


@Component({
  selector: 'app-run-depraciation-component',
  templateUrl: './run-depraciation-component.component.html',
  styleUrls: ['./run-depraciation-component.component.scss']
})
export class RunDepraciationComponentComponent implements OnInit {
  
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  NonAseetData: any;
  public isShow: boolean = false;
  public isQuarterly: boolean = false;
  public isYearly: boolean = false;
  public isHalfYearly: boolean = false;
  public isMonthly: boolean = false;
  constructor(public dialogRef: MatDialogRef<RunDepraciationComponentComponent>, private formBuilder: FormBuilder,
    private assetsServiceService: AssetsServiceService, private toastService: ToastNotificationService, private titleCasePipe: TitleCasePipe, private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    this.getrunsDepreaciteData()
  }

  getrunsDepreaciteData() {
    this.assetsServiceService.getNONCurrentAssetsData({}, this.currentUser.id, 5, 1).then((res: any) => {
      if (res.success) {
        this.NonAseetData = res.data?.equipmentSetting;
        if (this.NonAseetData) {
          this.isShow = true
          this.isMonthly = this.NonAseetData.monthly,
          this.isQuarterly = this.NonAseetData.quaterly,
          this.isYearly = this.NonAseetData.yearly,
          this.isHalfYearly = this.NonAseetData.halfyearly

          this.cdr.detectChanges();
        }
      }

    }, (err) => {
      if (err.error.expose || err.status == 404) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
      }
    })
  }

  cancel() {
    this.dialogRef.close();
  }

  saveRunDepraciation() {
    const requestBody = {
      userId: this.currentUser.id,
      monthly: this.isMonthly,
      yearly: this.isYearly,
      quaterly: this.isQuarterly,
      halfyearly: this.isHalfYearly,
    };

    if (!(requestBody.monthly || requestBody.yearly || requestBody.quaterly || requestBody.halfyearly)) {
      this.toastService.toastMsg({
        title: "Error",
        content: this.titleCasePipe.transform("Please Select at List One!!!"),
      });
      return false;
    }

    this.assetsServiceService.addRundeprec(requestBody, this.currentUser.id).then((res: any) => {
      this.toastService.toastMsg({
        title: "Success",
        content: 'Run Depreciation Added Successfully!!!',
      });
      this.isShow = true
      this.assetsServiceService.notifyassetsAdded();
      this.dialogRef.close();
    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
      }
    }
    )
  }

  updateRunDepraciation() {
    const requestBody = {
      userId: this.currentUser.id,
      monthly: this.isMonthly,
      yearly: this.isYearly,
      quaterly: this.isQuarterly,
      halfyearly: this.isHalfYearly,
    };
    if (!(requestBody.monthly || requestBody.yearly || requestBody.quaterly || requestBody.halfyearly)) {
      this.toastService.toastMsg({
        title: "Error",
        content: this.titleCasePipe.transform("Please Select at List One!!!"),
      });
      return false;

    } else {
      this.assetsServiceService.updateRundeprec(requestBody, this.currentUser.id).then((res: any) => {
        this.toastService.toastMsg({
          title: "Success",
          content: 'Run Depreciation Updated Successfully!!!',
        });
        this.assetsServiceService.notifyassetsAdded();
        this.dialogRef.close();
      }, (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
        }
      })
    }
  }

  updateRadio(event, value) {
    if (value === 'monthly') {
      this.isMonthly = event
      this.isYearly = !event
      this.isQuarterly = !event
      this.isHalfYearly = !event
    } else if (value === 'yearly') {
      this.isYearly = event
      this.isMonthly = !event
      this.isQuarterly = !event
      this.isHalfYearly = !event
    } else if (value === 'quarterly') {
      this.isQuarterly = event
      this.isMonthly = !event
      this.isYearly = !event
      this.isHalfYearly = !event
    } else if (value === 'halfYearly') {
      this.isHalfYearly = event
      this.isMonthly = !event
      this.isYearly = !event
      this.isQuarterly = !event
    }
  }
}
