import { Component, OnInit } from '@angular/core';
import { AssetFormcompoennetComponent } from '../asset-formcompoennet/asset-formcompoennet.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AssetEditFormcompoennetComponent } from '../asset-edit-formcompoennet/asset-edit-formcompoennet.component';
import { AssetsServiceService } from 'src/app/core/services/assets-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { DatePipe, TitleCasePipe } from '@angular/common';
import * as ExcelJS from 'exceljs/dist/exceljs.min.js';
import { saveAs } from 'file-saver';

@Component({
  selector: 'app-asset-listcompoennet',
  templateUrl: './asset-listcompoennet.component.html',
  styleUrls: ['./asset-listcompoennet.component.scss']
})
export class AssetListcompoennetComponent implements OnInit {
  AseetData: any=[];
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public currentPageNo: number = 0;
  public partyPageSize: number = 5;
  public p = 1;
  public totalrow: any;
  public toatlPages: any;
  public pagesize: number = 1;
  public assetId:any;
  searchKey: string;


  constructor(private dialog: MatDialog, private assetsServiceService: AssetsServiceService,private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe,private datePipe: DatePipe) { }

  ngOnInit(): void {
    this.assetsServiceService.assetsAdded$.subscribe(() => {
      this.getCurrentAssetsData();
    });
    this.getCurrentAssetsData();
  }
  getCurrentAssetsData(){
    this.assetsServiceService.getCurrentAssetsData({},this.currentUser.id, this.partyPageSize, (this.currentPageNo + 1)).then((res:any)=>{
    console.log("res");
    this.AseetData=res.data.pageData;
    this.p = res.data.currentPage;
    this.totalrow = res.data.totalrows
    this.toatlPages = res.data.totalPages


    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message)
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong."
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }
  OpenAddAssets(){
    const dialogRef: MatDialogRef<AssetFormcompoennetComponent> = this.dialog.open(AssetFormcompoennetComponent, {
      width: '50%',
      height: '100vh',
      data: {},
      panelClass: 'custom-dialog-assets-class',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
   
  }
  openEditModel(assetId){
    const dialogRef: MatDialogRef<AssetEditFormcompoennetComponent> = this.dialog.open(AssetEditFormcompoennetComponent, {
      width: '50%',
      height: '100vh',
      data: { assetId: assetId },
      panelClass: 'custom-dialog-assets-class',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }
  getcurrentAssetsId(assetId){
    this.assetId=assetId
  }
  deleteAsset(){
    this.assetsServiceService.deleteAssets({},this.assetId).then((res:any)=>{
      // this.toastService.openSnackBar("Current Assets Deleted Successfully!!!");
      this.toastService.toastMsg({
        title: "Success",
        content: "Current Assets Deleted Successfully!!!"
      });
      this.getCurrentAssetsData();

    }
    , (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message)
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong."
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
    
  }

  handlePage(e) {
    this.currentPageNo = e.pageIndex;
    this.pagesize = e.pageSize;
    if (this.AseetData.length > 0) {
      this.partyPageSize = e.pageSize;
      const searchInput = document.getElementById('searchInput') as HTMLInputElement;
      if (searchInput.value !== '') {
        this.getAssetListSearch(searchInput.value);
      } else {
        this.getCurrentAssetsData();
      }
     
    }
  }
 
  downlaodAllData() {
    if (this.AseetData.length > 0) {
      this.exportToExcel();  
    } else {
      this.toastService.toastMsg({
        title: "Error",
        content: "No Data Available For Download",
      });
     
    }
  }
  exportToExcel() {
    console.log("EXPORT ___")
    const workbook = new ExcelJS.Workbook();
    // const worksheet = workbook.addWorksheet('Sheet 1');
    const worksheet = workbook.addWorksheet('Sheet 1', {
      views: [
        {
          state: 'frozen',
          xSplit: 1, // Number of columns to freeze (0 means no freezing)
          ySplit: 1, // Number of rows to freeze (e.g., 2 means freeze the first two rows)
        },
      ],
    });
    // Define a header style
    const headerStyle = {
      font: { color: { argb: 'FF0000FF' }, bold: true },
      fill: { type: 'pattern', pattern: 'solid', fgColor: { argb: 'FFFF00FF' } },
    };
    // Define your headers
    const header1 = [
      'Date',
      'Asset Name',
      'Amount (In Rs.)',
    ]

    // Apply the header style to the header row
    const headerRow1 = worksheet.addRow(header1);
    headerRow1.eachCell((cell) => {
      cell.border = {
        top: { style: 'thin' },
        bottom: { style: 'thin' },
        left: { style: 'thin' },
        right: { style: 'thin' },
      };
      cell.fill = headerStyle.fill;
      cell.alignment = { vertical: 'middle', horizontal: 'center', wrapText: true };
      //  cell.width = 120

    });


    // Set column widths for header cells
    worksheet.columns.forEach((column, index) => {
      // Assuming you want a fixed width of 120 for all header columns
      column.width = 15;
    });

    // Set background colors for multiple cells using a loop
    const cellBackgroundColors = [
      { cell: 'A1', color: 'FFB4C6E7', width: 5 }, // green background for cell A1
      { cell: 'B1', color: 'FFB4C6E7' },// light blue background for cell A1
      { cell: 'C1', color: 'FFB4C6E7' },


    ];
    cellBackgroundColors.forEach((cellColor) => {
      worksheet.getCell(cellColor.cell).fill = {
        type: 'pattern',
        pattern: 'solid',
        fgColor: { argb: cellColor.color },
      };
    });
    this.AseetData.forEach((item, index) => {
      const row = [];
      const formattedInvoiceDate = this.datePipe.transform(item.date, 'dd MMM yyyy');
      row.push(formattedInvoiceDate ? formattedInvoiceDate : "NA");
      row.push(item.assetName ? item.assetName : "NA");
      row.push('₹ ' + (item.valueOfAsset ? parseFloat(item.valueOfAsset).toFixed(2) : '0.00'));
      worksheet.addRow(row);
      const vatCellC = worksheet.getCell('C' + worksheet.rowCount);
      // Set the alignment of the VAT cell to right
      vatCellC.alignment = { horizontal: 'right' };
    })
    workbook.xlsx.writeBuffer().then((buffer) => {
      const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
      saveAs(blob, 'Current Asset.xlsx');
    });
  }
  getAssetListSearch(searchKey) {
    if (this.searchKey == "") {
      this.currentPageNo = 0; 
      this.assetsServiceService.getCurrentAssetsDataFilter({}, this.currentUser.id, searchKey.trim(), this.partyPageSize, (this.currentPageNo + 1)).then((res: any) => {
        this.AseetData = res.data.pageData;
        this.p = res.data.currentPage;
        this.totalrow = res.data.totalrows
        this.toatlPages = res.data.totalPages;
        if (this.currentPageNo > 0) {
          if (this.AseetData.length === 0) {
            this.currentPageNo = 0;
            this.getAssetListSearch(this.searchKey);
          }
        }
      })
    }
    else {
      this.currentPageNo = 0; 
      this.assetsServiceService.getCurrentAssetsDataFilter({}, this.currentUser.id, searchKey.trim(), this.partyPageSize, (this.currentPageNo + 1)).then((res: any) => {
        this.AseetData = res.data.pageData;
        this.p = res.data.currentPage;
        this.totalrow = res.data.totalrows
        this.toatlPages = res.data.totalPages
        if (this.currentPageNo > 0) {
          if (this.AseetData.length === 0) {
            this.currentPageNo = 0;
            this.getAssetListSearch(this.searchKey);
          }
        }
      }, (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
        }

      }
      );

    }


  }

}
